.Ethvelope {
  text-align: center;
  position: relative;
}

.Ethvelope-image {
  width: 100%;
}

.Ethvelope-actions {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
	background-color: rgba(255,255,255, 0.85);
	background-opacity: 0.2;
}

.Ethvelope:hover .Ethvelope-actions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Ethvelope-form {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-line-pack: baseline;
	    align-content: baseline;
}



